<template>
  <div  style="overflow: hidden;" class="MarketE-page">         
      <MarketEView ref='markete'/>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
// components
import MarketEView from './../../view/MarketE';

import poolListConfig from '../../../../config/pool-list';


export default {
	name: 'markets',
	components: {
	    MarketEView,
	},
  	computed: {
      ...mapGetters([
        'borrowerDetail'
      ])
    },
    created() {    
     
    },
    mounted () {
      let order = this.$route.params.order;
      this.initData(order);
    },
    methods: {
    	...mapActions([
          'initBorrowerDetail',
          'initBorrowerBalance',
          'initBorrowerTransactions',
          'setPoolDetail'
      	]),
      	initData(order) {
          let poolDetail = this.getPoolDetail(order);
          this.initBorrowerBalance();
          if (poolDetail && poolDetail.type == 0) {
            this.setPoolDetail(poolDetail);
            if (poolDetail.poolContractAddr && poolDetail.poolContractAddr != '') {
              this.initBorrower(poolDetail.poolContractAddr, poolDetail.tokenSupportDecimal);
            }
          }
      	},
        initBorrower(borrowPoolContractAddr, tokenDecimal) {
          this.initBorrowerDetail({ borrowPoolContractAddr, tokenDecimal });
          this.initBorrowerTransactions({ borrowPoolContractAddr, tokenDecimal });
        },
        getPoolDetail(order) {
          let poolDetail;
          for (let idx = 0; idx < poolListConfig.length; idx++) {
            if (order == poolListConfig[idx].order) {
              poolDetail = poolListConfig[idx];
              break;
            }
          }
          return poolDetail;
        }
    }
}
</script>

<style lang="scss">
</style>


