<template>
      <div class="market clearfix">
      	<div class="container">
          	<div class="row">
              <div class="col-6">
                <div class="e-chart">
                  <h4 class="name"><img :src="poolDetail.icon" class="token-logo">{{ poolDetail.tokenSupportName }}</h4>
                  <div class="chart">
                    <!-- <img src="../../assets/images/chart.png"> -->
                    <div class="infor">
                      <ul>
                        <li>{{$t('message.label.liquidationRatio')}}<span>{{ poolDetail.liquidationRatio ? poolDetail.liquidationRatio : 0 }} %</span></li>
                        <li>{{$t('message.label.apr')}} <span>{{ poolDetail.apr ? poolDetail.apr : 0 }} %</span></li>
                        <li>{{$t('message.label.exchangePrice')}} <span>{{ numberWithCommas(poolDetail.exchangePrice, 6) }} {{ poolDetail.assetName }}</span></li>
                        <!-- <li>Price <span>$182.44</span></li>
                        <li>Price <span>$182.44</span></li> -->
                      </ul>
                    </div>
                  </div>
                </div>
              </div> 
              <div class="col-6">
                <div class="balance-and-transactions">
                  <div class="balance">
                    <h4 class="title">{{$t('message.label.supplyBalance')}} <span class="price">{{ numberWithCommas(borrowerDetail.tokensCollateral) }} {{ poolDetail.tokenSupportName }}</span></h4>
                    <div class="button">
                        <button class="button-app supply mb-1" v-if="isShowEnableBtn()" @click="enable">{{$t('message.button.enable')}}</button>
                        <button class="button-app supply mb-1" v-if="isShowDepositBtn()" @click="showModal('modal-borrower-deposit')">{{$t('message.button.deposit')}}</button>
                        <button class="button-app supply mb-1" v-if="isShowBorrowBtn()" @click="showModal('modal-borrow')">{{$t('message.button.borrow')}}</button>
                        <button class="button-app supply mb-1" v-if="isShowRepayBtn()" @click="showModal('modal-repay')">{{$t('message.button.repay')}}</button>
                        <button class="button-app supply mb-1" v-if="isShowWithdrawBtn()" @click="withdraw">{{$t('message.button.withdraw')}}</button>
                        <button class="button-app supply mb-1" v-if="isShowStakeBtn()" @click="stake">{{$t('message.button.stake')}}</button>
                        <button class="button-app supply mb-1" v-if="isShowUnstakeBtn()" @click="unstake">{{$t('message.button.unstake')}}</button>
                    </div>
                    <div class="infor">
                      <ul>
                        <li>{{$t('message.label.yourBalance')}} <span>{{ numberWithCommas(borrowerBalance, 6) }} {{ poolDetail.assetName }}</span></li>
                        <li>{{$t('message.label.principal')}} <span>{{ numberWithCommas(borrowerDetail.principal, 6) }} {{ poolDetail.assetName }}</span></li>
                        <li>{{$t('message.label.amountOwed')}} <span>{{ numberWithCommas(borrowerDetail.amountOwed, 6) }} {{ poolDetail.assetName }}</span></li>
                        <li>{{$t('message.label.collateralisationRatio')}} <span>{{ numberWithCommas(borrowerDetail.collateralisationRatio, 2) }}%</span></li>
                        <li>{{$t('message.label.tokenStakeToDapp')}} <span>{{ numberWithCommas(borrowerDetail.tokenStakeToDapp, 3) }} {{ poolDetail.tokenSupportName }}</span></li>
                      </ul>
                    </div>
                  </div>
                  <div class="transactions">
                    <h4 class="title">{{$t('message.label.transactions')}}</h4>
                    <div class="infor">
                      <ul>
                        <li v-for="item in borrowerTransactions">
                          <span class="icon-transaction"></span>
                          <span class="description-transaction">{{ item.description }}</span>
                          <span class="time-transaction">{{ convertTime(item.time) }}</span>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
        </div>
        <!-- Modal Deposit Start -->
        <b-modal ref="modal-borrower-deposit" class="modal-zethyr-lending" hide-footer hide-header title="Locked">

          <div class="d-block mheader">
            <b-row align-h="between" class="header">
              <b-col cols='9' >
                <h4 class="title">{{$t('message.label.modalDepositTitle')}}</h4>
              </b-col>
              <b-col cols='3'>
                <button type="button" aria-label="Close" class="close" @click="hideModal('modal-borrower-deposit')">x</button>
              </b-col>
            </b-row>
          </div> 
          <div class="mContent">
            <p class="text-left" >{{$t('message.label.available')}}</p>
            <h4>{{ numberWithCommas(borrowerDetail.borrowerTokens) }} {{ poolDetail.tokenSupportName }}</h4>
            <b-form-group class="text-left">
              <div class="label">
                <p>{{$t('message.label.enter')}}</p>
              </div>  
              <div class="input-amount">
                <b-form-input :value="tokensToDeposit" name="tokensToDeposit" placeholder="0.000" type="number" step="0.001" min="0.001"></b-form-input>
                <span class="error mt-3">{{ err.tokensToDeposit }}</span>
              </div>                              
            </b-form-group>
            <b-button class="btn-zethyr-lending mb-3" style="width: 100% !important" @click="depositTokens" >{{$t('message.button.deposit')}}</b-button>
          </div>  
        </b-modal>
        <!-- Modal Deposit End -->
        <!-- Modal Borrow Start -->
        <b-modal ref="modal-borrow" class="modal-zethyr-lending" hide-footer hide-header title="Locked">

          <div class="d-block mheader">
            <b-row align-h="between" class="header">
              <b-col cols='9' >
                <h4 class="title">{{$t('message.label.modalBorrowTitle')}}</h4>
              </b-col>
              <b-col cols='3'>
                <button type="button" aria-label="Close" class="close" @click="hideModal('modal-borrow')">x</button>
              </b-col>
            </b-row>
          </div> 
          <div class="mContent">
            <p class="text-left" >{{$t('message.label.borrowPower')}}</p>
            <h4>{{ numberWithCommas(borrowerDetail.borrowingPower, 0) }} {{ poolDetail.assetName }}</h4>
            <b-form-group class="text-left">
              <div class="label">
                <p>{{$t('message.label.enter')}}</p>
              </div>  
              <div class="input-amount">
                <b-form-input :value="amountToBorrow" name="amountToBorrow" placeholder="0" type="number" step="1" min="1"></b-form-input>
                <span class="error mt-3">{{ err.amountToBorrow }}</span>
              </div>                              
            </b-form-group>
            <b-button class="btn-zethyr-lending mb-3" style="width: 100% !important" @click="borrow" >{{$t('message.button.borrow')}}</b-button>
          </div>  
        </b-modal>
        <!-- Modal Borrow End -->
        <!-- Modal Repay Start -->
        <b-modal ref="modal-repay" class="modal-zethyr-lending" hide-footer hide-header title="Locked">

          <div class="d-block mheader">
            <b-row align-h="between" class="header">
              <b-col cols='9' >
                <h4 class="title">{{$t('message.label.modalRepayTitle')}}</h4>
              </b-col>
              <b-col cols='3'>
                <button type="button" aria-label="Close" class="close" @click="hideModal('modal-repay')">x</button>
              </b-col>
            </b-row>
          </div> 
          <div class="mContent">
            <p class="text-left" >{{$t('message.label.amountOwed')}}</p>
            <h4>{{ numberWithCommas(borrowerDetail.amountOwed, 6) }} {{ poolDetail.assetName }}</h4>
            <p class="text-left" >{{$t('message.label.principal')}}</p>
            <h4>{{ numberWithCommas(borrowerDetail.principal, 6) }} {{ poolDetail.assetName }}</h4>
            <b-form-group class="text-left">
              <div class="label">
                <p>{{$t('message.label.enter')}}</p>
              </div>  
              <div class="input-amount">
                <b-form-input :value="amountToRepay" name="amountToRepay" placeholder="0" type="number" step="1" min="1"></b-form-input>
                <span class="error mt-3">{{ err.amountToRepay }}</span>
              </div>                              
            </b-form-group>
            <b-button class="btn-zethyr-lending mb-3" style="width: 100% !important" @click="repay" >{{$t('message.button.repay')}}</b-button>
          </div>  
        </b-modal>
        <!-- Modal Repay End -->
      </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import ZethyrLendingContract  from '../../js/zethyr-lending.js';
import ZethyrStakeWinContract  from '../../js/zethyr-stake-win.js';
import UtilFormat from '../../utils/format';
import UtilConvertTime from '../../utils/convertTime';
import MYTronWeb from "../../js/tron-web";

export default {
  computed: {
    ...mapGetters([
      'borrowerDetail',
      'borrowerTransactions',
      'borrowerBalance',
      'poolDetail'
    ])
  },
  data () {
    return {
      err: {
        tokensToDeposit: '',
        amountToRepay: '',
        amountToBorrow: ''
      },
      tokensToDeposit: '',
      amountToRepay: '',
      amountToBorrow: ''
    }
  },
  created() {    
    
  },
  mounted () {
    this.makeJqueryEvent();
  },
  methods: {
    ...mapActions([
          'initBorrowerDetail',
          'initBorrowerTransactions'
        ]),
    /** -------------------------------------------------------------------
    *   --------------------- CHECK DISPLAY UI ----------------------------
    *   -------------------------------------------------------------------
    */
    isShowEnableBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return true;
      if (!borrowerDetail.isEnable) return true;
      if (borrowerDetail.isEnable == false) return true;
      return false;
    },
    isShowDepositBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return false;
      if (!borrowerDetail.isEnable) return false;
      if (borrowerDetail.isEnable == false) return false;
      return true;
    },
    isShowBorrowBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return false;
      if (!borrowerDetail.isEnable) return false;
      if (borrowerDetail.isEnable == false) return false;
      if (borrowerDetail.isLiquidation == true) return false;
      if (!borrowerDetail.tokensCollateral) return false;
      if (borrowerDetail.tokensCollateral <= 0) return false;
      if (borrowerDetail.dayToRepayInterest > 0) return false;
      return true;
    },
    isShowRepayBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return false;
      if (!borrowerDetail.isEnable) return false;
      if (borrowerDetail.isEnable == false) return false;
      if (!borrowerDetail.principal) return false;
      if (borrowerDetail.principal <= 0) return false;
      return true;
    },
    isShowWithdrawBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return false;
      if (!borrowerDetail.isEnable) return false;
      if (borrowerDetail.isLiquidation == true) return false;
      if (borrowerDetail.isEnable == false) return false;
      if (!borrowerDetail.tokensCollateral) return false;
      if (borrowerDetail.tokensCollateral <= 0) return false;
      if (borrowerDetail.principal > 0) return false;
      if (borrowerDetail.tokenStakeToDapp <= 0) return false;
      return true;
    },
    isShowStakeBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return false;
      if (!borrowerDetail.isEnable) return false;
      if (borrowerDetail.isLiquidation == true) return false;
      if (borrowerDetail.isEnable == false) return false;
      if (!borrowerDetail.tokensCollateral) return false;
      if (borrowerDetail.tokensCollateral <= 0) return false;
      if (borrowerDetail.tokensCollateral <= borrowerDetail.tokenStakeToDapp) return false;
      if (poolDetail.stakeToDappStatus == false) return false;
      return true;
    },
    isShowUnstakeBtn() {
      let poolDetail = this.poolDetail;
      let borrowerDetail = this.borrowerDetail;
      let borrowerAddr = MYTronWeb.getAccount();
      if (!poolDetail) return false;
      if (!borrowerAddr || borrowerAddr == process.env.ADDRESS_DEFAULT) return false;
      if (!borrowerDetail) return false;
      if (!borrowerDetail.isEnable) return false;
      if (borrowerDetail.isLiquidation == true) return false;
      if (borrowerDetail.isEnable == false) return false;
      if (!borrowerDetail.tokensCollateral) return false;
      if (borrowerDetail.tokensCollateral <= 0) return false;
      if (borrowerDetail.tokenStakeToDapp <= 0) return false;
      if (poolDetail.stakeToDappStatus == false) return false;
      return true;
    },
    numberWithCommas(x, decimal = 3) {
      return UtilFormat.numberWithCommas(x, decimal);
    },
    convertTime(time) {
      return UtilConvertTime.convertTransactionTime(time);
    },
    /** -------------------------------------------------------------------
    *   --------------------- JQUERY ACTION ---------------------------------
    *   -------------------------------------------------------------------
    */
    makeJqueryEvent() {
        const REGEX_INT =  /\d{0,99}(\.\d{0,0})?/;
        const REGEX_FLOAT = /\d{0,99}(\.\d{0,3})?/;
        const REGEX_AMOUNT = /\d{0,99}(\.\d{0,6})?/;
        let self = this;
        $(function() {
          makeEventChangeTokensToDeposit();
          makeEventChangeAmountToBorrow();
          makeEventChangeAmountToRepay();
        });
        function makeEventChangeTokensToDeposit() {
          $('input[type=number][name="tokensToDeposit"]').on('input', function() {
            this.value = this.value.match(REGEX_FLOAT)[0];
            self.tokensToDeposit = this.value;
          });
        } 
        function makeEventChangeAmountToBorrow() {
          $('input[type=number][name="amountToBorrow"]').on('input', function() {
            this.value = this.value.match(REGEX_INT)[0];
            self.amountToBorrow = this.value;
          });
          
        }
        function makeEventChangeAmountToRepay() {
          $('input[type=number][name="amountToRepay"]').on('input', function() {
            this.value = this.value.match(REGEX_AMOUNT)[0];
            self.amountToRepay = this.value;
          });
          
        }
      },
    /** -------------------------------------------------------------------
    *   --------------------- USER ACTION ---------------------------------
    *   -------------------------------------------------------------------
    */
    reloadData() {
      let self = this;
      let poolDetail = this.poolDetail;
      if (poolDetail) {
        setTimeout(function(){ 
          self.initBorrowerDetail({ borrowPoolContractAddr: poolDetail.poolContractAddr, tokenDecimal: poolDetail.tokenSupportDecimal });
          self.initBorrowerTransactions({ borrowPoolContractAddr: poolDetail.poolContractAddr, tokenDecimal: poolDetail.tokenSupportDecimal });
        }, 3000);
      }
    },
    hideModal(modalRef) {
      this.$refs[modalRef].hide();
    },
    showModal(modalRef) {
      let err = this.err ? this.err : {};
      if (modalRef == 'modal-borrower-deposit') {
        err.tokensToDeposit = '';
        this.tokensToDeposit = '';
      }
      if (modalRef == 'modal-borrow') {
        err.amountToBorrow = '';
        this.amountToBorrow = '';
      }
      if (modalRef == 'modal-repay') {
        err.amountToRepay = '';
        this.amountToRepay = '';
      }
      this.err = err;
      this.$refs[modalRef].show();
    },
    enable() {
      let self = this;
      ZethyrLendingContract.enable();
      function response() {
        // self.initBorrowerDetail({ borrowPoolContractAddr: 'TK9ZgAY2mCYBGfX92gf6Ku7hNaiWRSmEZN' });
      } 
      function error(e) {
        console.log(e);
      } 
    },
    depositTokens() {
      let self = this;
      let err = {};
      let tokensToDeposit = this.tokensToDeposit;
      let borrowerTokens = this.borrowerDetail && this.borrowerDetail.borrowerTokens ? this.borrowerDetail.borrowerTokens : 0;
      if (tokensToDeposit <= 0) {
        err.tokensToDeposit = 'Invalid Value';
        return this.err = err;
      }
      if (tokensToDeposit > borrowerTokens) {
        err.tokensToDeposit = 'Balance Not enough!';
        return this.err = err; 
      }
      ZethyrLendingContract.borrowerDeposit(this.poolDetail, tokensToDeposit, err => {
        if (err) return error(err);
        return response();
      });

      function response() {
        self.hideModal('modal-borrower-deposit');
        self.reloadData();
      } 
      function error(e) {
        console.log(e);
      } 
    },
    borrow() {
      let self = this;
      let err = {};
      let amountToBorrow = this.amountToBorrow;
      let borrowingPower = this.borrowerDetail && this.borrowerDetail.borrowingPower ? this.borrowerDetail.borrowingPower : 0;
      if (amountToBorrow <= 0) {
        err.amountToBorrow = 'Invalid Value';
        return this.err = err;
      }
      if (amountToBorrow > borrowingPower) {
        err.tokensToDeposit = `You just only to borrow maximum ${ numberWithCommas(borrowingPower, 0)}`;
        return this.err = err; 
      }
      ZethyrLendingContract.borrowerBorrow(this.poolDetail, amountToBorrow, err => {
        if (err) return error(err);
        return response();
      });

      function response() {
        self.hideModal('modal-borrow');
        self.reloadData();
      } 
      function error(e) {
        console.log(e);
      } 
    },
    repay() {
      let self = this;
      let err = {};
      let amountToRepay  = this.amountToRepay;
      let principal = this.borrowerDetail && this.borrowerDetail.principal ? this.borrowerDetail.principal : 0;
      let amountOwed = this.borrowerDetail && this.borrowerDetail.amountOwed ? this.borrowerDetail.amountOwed : 0;
      if (amountToRepay <= 0) {
        err.amountToRepay = 'Invalid Value';
        return this.err = err;
      }
      if (amountToRepay < amountOwed - principal) {
        err.amountToRepay = 'Invalid Repay';
        return this.err = err;
      }
      ZethyrLendingContract.borrowerRepay(this.poolDetail, amountToRepay, err => {
        if (err) return error(err);
        return response();
      });

      function response() {
        self.hideModal('modal-repay');
        self.reloadData();
      } 
      function error(e) {
        console.log(e);
      } 
    },
    stake() {
      let self = this;
      let poolDetail = this.poolDetail;
      if (!poolDetail) return false;
      if (poolDetail.stakeToDappStatus == false) return false;
      if (poolDetail.tokenSupportAddr.toLowerCase() == process.env.WIN_CONTRACT_ADDR.toLowerCase()) {
        stakeWin();
      }
      function stakeWin() {
        if (!ZethyrStakeWinContract || !ZethyrStakeWinContract.CONTRACT) return false;
        ZethyrStakeWinContract
          .stakeWin()
          .then(response)
          .catch(error);
      }
      function response() {
        self.reloadData();
      }
      function error(e) {
        console.log(e);
      }
    },
    unstake() {
       let self = this;
      let poolDetail = this.poolDetail;
      if (!poolDetail) return false;
      if (poolDetail.stakeToDappStatus == false) return false;
      if (poolDetail.tokenSupportAddr.toLowerCase() == process.env.WIN_CONTRACT_ADDR.toLowerCase()) {
        unfreezeWin();
      }
      function unfreezeWin() {
        if (!ZethyrStakeWinContract || !ZethyrStakeWinContract.CONTRACT) return false;
        ZethyrStakeWinContract
          .unfreezeWin()
          .then(response)
          .catch(error);
      }
      function response() {
        self.reloadData();
      }
      function error(e) {
        console.log(e);
      }
    },
    withdraw() {
      let self = this;
      ZethyrLendingContract
        .borrowerWithdraw(this.poolDetail)
        .then(res => {
          self.reloadData();
        }).catch(e => console.log(e));
    }
  }
  
}
</script>


