function borrowerDetail(state) {
	return state.borrower.borrowerDetail;
}
function borrowerBalance(state) {
	return state.borrower.borrowerBalance;
}
function borrowerTransactions(state) {
	return state.borrower.borrowerTransactions;
}
export default {
	borrowerDetail,
	borrowerTransactions,
	borrowerBalance
}


