import Vue from 'vue'
import Router from 'vue-router'

import HomeContainer from '../containers/Home'
import MarketEContainer from '../containers/MarketE'
import TableContainer from '../containers/Table'
import TermContainer from '../containers/Term'

Vue.use(Router)

const HomeRoute    = { path: '/', name: 'home', component: HomeContainer, meta: { title: 'Zethyr Lending' } };
const MarketERoute    = { path: '/market-borrow/:order', name: 'market-borrow', component: MarketEContainer, meta: { title: 'Zethyr Lending' } };
const TableERoute    = { path: '/markets', name: 'markets', component: TableContainer, meta: { title: 'Zethyr Lending' } };
const TermRoute    = { path: '/term', name: 'term', component: TermContainer, meta: { title: 'Zethyr Lending' } };

const routes = [
	HomeRoute,
	MarketERoute,
	TableERoute,
	TermRoute
];
const router = new Router({
  // mode: 'history', // require service support
  // scrollBehavior: () => ({ y: 0 }),
  routes: routes,
});

router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  next();
});
export default router;



