import MYTronWeb from "./tron-web";
export default {
	CONTRACT_ADDRESS :  process.env.ZETHYR_STAKE_WIN_CONTRACT_ADDR,
	CONTRACT         : null,
  CONTRACT_CALL_DATA : null,
	initContract(callback) {
      let self = this;
      if (typeof(window.tronWeb) === 'undefined') {
          setTimeout(() => {
            self.initContract(callback);
          }, 100);
      } else {
        tronWeb
          .contract()
          .at(self.CONTRACT_ADDRESS)
          .then(response)
          .catch(error);
        function response(tronContract) {
          self.CONTRACT = tronContract;
          return callback(true);
        } 
        function error(e) { 
          return callback(false); 
        }
      }
    },
    initContractCallData(tronWebByZethyrAccount, callback) {
      let self = this;
      tronWebByZethyrAccount
        .contract()
        .at(self.CONTRACT_ADDRESS)
        .then(response)
        .catch(error);
      function response(tronContract) {
          self.CONTRACT_CALL_DATA = tronContract;
          return callback(true);
      } 
      function error(e) { 
          return callback(false); 
      }
    },
    /** ----------------------------------------------------------------------
    *   --------------------------- CALL -------------------------------------
    *   ----------------------------------------------------------------------
    */
    /** ----------------------------------------------------------------------
    *   --------------------------- ACTION -------------------------------------
    *   ----------------------------------------------------------------------
    */
    stakeWin() {
      return this.CONTRACT.stakeWin().send({ feeLimit: parseInt(process.env.FEE_LIMIT) });
    },
    unfreezeWin() {
      return this.CONTRACT.unfreezeWin().send({ feeLimit: parseInt(process.env.FEE_LIMIT) });
    }
}



