<template>
  <div class="exchange-world">
      <b-container>
          <div class="row exchange-top">
              <div class="col-4 item">
                  <div class="exchange-top-title d-flex align-items-center justify-content-between">{{$t('message.label.supplyBalance')}} ({{ poolDetail.tokenSupportName}})</div>
                  <div class="exchange-top-subtitle">${{ numberWithCommas(borrowerDetail.tokensCollateral) }}</div>
              </div>
              <div class="col-4 item">
                  <div class="exchange-top-title d-flex align-items-center justify-content-between">{{$t('message.label.borrowBalance')}} ({{ poolDetail.assetName}})</div>
                  <div class="exchange-top-subtitle">${{ numberWithCommas(borrowerDetail.principal, 6) }}</div>
              </div>
              <div class="col-4 item">
                  <div class="exchange-top-title d-flex align-items-center justify-content-between">{{$t('message.label.borrowPower')}} ({{ poolDetail.assetName}})</div>
                  <div class="exchange-top-subtitle">${{ numberWithCommas(borrowerDetail.borrowingPower, 6) }}</div>
              </div>
          </div>
      </b-container>
  </div>
</template>
<script>
  import { mapGetters, mapActions } from 'vuex';
  import UtilFormat from '../../utils/format';

  export default {
    computed: {
      ...mapGetters([
        'borrowerDetail',
        'poolDetail'
      ])
    },
    data () {
      return {
       
      }
    },
    methods: {
      numberWithCommas(x, decimal = 3) {
        return UtilFormat.numberWithCommas(x, decimal);
      },
    }
    
  }
</script>



