import ZethyrLendingContract  from '../../js/zethyr-lending.js';
import MYTronWeb from "../../js/tron-web";
import poolListConfig from '../../../../config/pool-list';
const Store = {
  state: {
    poolList: [],
    poolDetail: {},
  },
  mutations: {
    SET_POOL_LIST: ( state, data ) => state.poolList     = data,
    SET_POOL_DETAIL: ( state, data ) => state.poolDetail     = data,
  },
  actions: {
    
    initPoolList({ commit }, { page, limit }) {
      Store.actions.setPoolList({ commit }, poolListConfig);
    },  

    setPoolList({ commit }, data) {
      commit('SET_POOL_LIST', data);
    },
    setPoolDetail({ commit }, data) {
      commit('SET_POOL_DETAIL', data);
    }
  }
};

export default Store;



