import ZethyrLendingContract  from '../../js/zethyr-lending.js';
import ZethyrToolContract  from '../../js/zethyr-tool.js';
import MYTronWeb from "../../js/tron-web";
const Store = {
  state: {
    borrowerDetail: {},
    borrowerBalance: 0,
    borrowerTransactions: [],
  },
  mutations: {
    SET_BORROWER_DETAIL: ( state, data ) => state.borrowerDetail     = data,
    SET_BORROWER_BALANCE: ( state, data ) => state.borrowerBalance     = data,
    SET_BORROWER_TRANSACTIONS: ( state, data ) => state.borrowerTransactions     = data,
  },
  actions: {
    
    initBorrowerDetail({ commit }, { borrowPoolContractAddr, tokenDecimal }) {
      if (!ZethyrLendingContract || !ZethyrLendingContract.CONTRACT_CALL_DATA || MYTronWeb.getAccount() == process.env.ADDRESS_DEFAULT || !MYTronWeb.getAccount()) {
        return setTimeout(function(){ 
          Store.actions.initBorrowerDetail({ commit }, { borrowPoolContractAddr, tokenDecimal });
        }, 300); 
      }
      ZethyrLendingContract.getBorrowerDetail(borrowPoolContractAddr, tokenDecimal, (err, borrowerDetail) => {
        if (!err && borrowerDetail) {
          Store.actions.setBorrowerDetail({ commit }, borrowerDetail);
        }
      });
    },
    initBorrowerBalance({ commit }) {
      if (!ZethyrToolContract || !ZethyrToolContract.CONTRACT_CALL_DATA || MYTronWeb.getAccount() == process.env.ADDRESS_DEFAULT || !MYTronWeb.getAccount()) {
        return setTimeout(function(){ 
          Store.actions.initBorrowerBalance({ commit });
        }, 300); 
      }

      ZethyrToolContract.getBalance(MYTronWeb.getAccount(), (err, balance) => {
        if (!err && balance) {
          Store.actions.setBorrowerBalance({ commit }, balance);
        }
      });
    },  
    initBorrowerTransactions({ commit }, { borrowPoolContractAddr, tokenDecimal }) {
      let data = [
        {
          status: 1,
          description: 'Repay 0.0152 ZET',
          amount: 0.0152,
          assetName: 'ZET',
          time: 1574679047
        },
        {
          status: 1,
          description: 'Borrow 1.6892 ZET',
          amount: 0.0152,
          assetName: 'ZET',
          time: 1574679047
        },
        {
          status: 0,
          description: 'Enable For Borrowing',
          amount: 0.0152,
          assetName: 'ZET',
          time: 1574679047
        },
      ];
      Store.actions.setBorrowerTransactions({ commit }, data);
    },


    setBorrowerDetail({ commit }, data) {
      commit('SET_BORROWER_DETAIL', data);
    },
    setBorrowerBalance({ commit }, data) {
      commit('SET_BORROWER_BALANCE', data);
    },
    setBorrowerTransactions({ commit }, data) {
      commit('SET_BORROWER_TRANSACTIONS', data);
    }
  }
};

export default Store;



