import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
import home from './modules/home'
import borrower from './modules/borrower'
import pool from './modules/pool'
Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
    home,
    borrower,
    pool
  },
  getters
})

export default store



