<template>
      <div class="market clearfix">
      	<div class="container">
          <!-- 	<div class="row">
              <div class="col-12">
                <div class="e-chart">
                  <h4 class="name">Supplying</h4>
                  <div class="chart">
                    <div class="infor">
                       <table class="table">
                        <tr>
                          <th class="stt">Market</th>
                          <th>Rate</th>
                          <th>Earned</th> 
                          <th>Balance</th>
                        </tr>
                        <tr  class="cursor">
                          <td class=" stt"><img src="../../assets/images/icon-eth.png">Ether</td>
                          <td>0.04%</td>
