              </div>  -->
              <div class="col-12">
                <div class="e-chart">
                  <h4 class="name">{{$t('message.label.borrowing')}}</h4>
                  <div class="chart">
                    <div class="infor">
                       <table class="table">
                        <tr>
                          <th class="stt">{{$t('message.label.market')}}</th>
                          <th>{{$t('message.label.apr')}}</th>
                          <th>Earned</th> 
                          <th>Balance</th>
                        </tr>
                        <tr  class="cursor">
                          <td class=" stt"><img src="../../assets/images/icon-eth.png">Ether</td>
                          <td>0.04%</td>
                          <td>0 ETH</td> 
                          <td >0.0152 ETH</td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-12">
                <div class="e-chart">
                  <h4 class="name">{{$t('message.label.available')}}</h4>
                  <div class="chart">
                    <div class="infor">
                       <table class="table">
                        <tr>
                          <th class="stt">{{$t('message.label.market')}}</th>
                          <th>{{$t('message.label.marketSize')}}</th>
                          <th>{{$t('message.label.apr')}}</th> 
                          <th>{{$t('message.label.type')}}</th>
                        </tr>
                        <tr  class="cursor" v-for="item in poolList" @click="redirectToPoolDetail(item.order)">
                          <td class="stt"><img :src="item.icon" class="token-logo"><span class="ml-1">{{ item.tokenSupportName }}</span></td>
                          <td>{{ item.size }}</td>
                          <td>{{ item.apr }}%</td> 
                          <td>{{ (item.type == 0) ? 'Borrow' : 'Supply' }}</td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
        </div>
      </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters([
      'poolList'
    ])
  },
  data () {
    return {
     
    }
  },
  methods: {
    redirectToPoolDetail(order) {
       this.$router.push({name:'market-borrow', params: { order: order }});
    }  
  }
  
}
</script>


