import MYTronWeb from "./tron-web";
export default {
	CONTRACT_ADDRESS :  process.env.ZETHYR_LENDING_CONTRACT_ADDR,
	CONTRACT         : null,
  CONTRACT_CALL_DATA : null,
	initContract(callback) {
      let self = this;
      if (typeof(window.tronWeb) === 'undefined') {
          setTimeout(() => {
            self.initContract(callback);
          }, 100);
      } else {
        tronWeb
          .contract()
          .at(self.CONTRACT_ADDRESS)
          .then(response)
          .catch(error);
        function response(tronContract) {
          self.CONTRACT = tronContract;
          return callback(true);
        } 
        function error(e) { 
          return callback(false); 
        }
      }
    },
    initContractCallData(tronWebByZethyrAccount, callback) {
      let self = this;
      tronWebByZethyrAccount
        .contract()
        .at(self.CONTRACT_ADDRESS)
        .then(response)
        .catch(error);
      function response(tronContract) {
          self.CONTRACT_CALL_DATA = tronContract;
          return callback(true);
      } 
      function error(e) { 
          return callback(false); 
      }
    },
    /** ----------------------------------------------------------------------
    *   --------------------------- CALL -------------------------------------
    *   ----------------------------------------------------------------------
    */
    getBorrowerDetail(poolAddr, tokenDecimal, callback) {
      let self = this;
      loadData();
      function loadData() {
        _getBorrowerDetail();
        function _getBorrowerDetail(walletDetail) {
          self.CONTRACT_CALL_DATA
            .getBorrowerDetail(MYTronWeb.getAccount(), poolAddr)
            .call()
            .then(_getBorrowerMoreInfo)
            .catch(error);
        }
        function _getBorrowerMoreInfo(borrowerDetail) {
           self.CONTRACT_CALL_DATA
            .getBorrowerData(MYTronWeb.getAccount(), poolAddr)
            .call()
            .then(borrowerData => handle(borrowerData, borrowerDetail))
            .catch(error);
          
        }
      } 
      function handle(borrowerData, borrowerDetail) {
        let data = {
          "zethyrWalletAddr"  : MYTronWeb.toAddress(borrowerData.zethyrWalletAddr),
          "isEnable"          : borrowerData.isEnable,
          "tokensCollateral"  : parseInt(borrowerDetail.tokensCollateral) / (10 ** parseInt(tokenDecimal)), 
          "borrowerTokens"  : parseInt(borrowerData.borrowerTokens) / (10 ** parseInt(tokenDecimal)), 
          "tokenStakeToDapp"  : parseInt(borrowerData.tokensStakeToDapp) / (10 ** parseInt(tokenDecimal)), 
          "collateralisationRatio": parseInt(borrowerData.collateralisationRatio),
          "principal"         : MYTronWeb.toTrx(borrowerDetail.principal), 
          "isLiquidation"     : borrowerDetail.isLiquidation, 
          "borrowingPower"    : MYTronWeb.toTrx(borrowerDetail.borrowingPower), 
          "amountOwed"        : MYTronWeb.toTrx(borrowerDetail.amountOwed), 
          "dayToRepayInterest": parseInt(borrowerDetail.dayToRepayInterest), 
          "numberDayToBorrow" : parseInt(borrowerDetail.numberDayToBorrow)
        };
        return response(data);
      }
      function response(data) {
        return callback(null, data);
      }
      function error(e) {
        return callback(e, null); 
      } 
    },
    /** --------------------------------------------------------------------------
    *   ---------------------------- ACTION --------------------------------------
    *   --------------------------------------------------------------------------
    */
    enable() {
      this.CONTRACT.borrowerEnable().send({ feeLimit: parseInt(process.env.FEE_LIMIT) });
    },
    borrowerDeposit(poolDetail, tokensToDeposit, callback) {
      let self = this;
      let tokenDecimal     = poolDetail && poolDetail.tokenSupportDecimal ? poolDetail.tokenSupportDecimal : 1; 
      let tokenSupportType = poolDetail && poolDetail.tokenSupportType ? poolDetail.tokenSupportType : -1;
      let poolContractAddr = poolDetail && poolDetail.poolContractAddr ? poolDetail.poolContractAddr : '';
      let tokenSupportAddr = poolDetail && poolDetail.tokenSupportAddr ? poolDetail.tokenSupportAddr : '';
      let collateralContractAddr = poolDetail && poolDetail.collateralContractAddr ? poolDetail.collateralContractAddr : '';
      let tokenSupportId = poolDetail && poolDetail.tokenSupportId ? poolDetail.tokenSupportId : 0; 
      if (tokenSupportType != 1 && tokenSupportType != 2) return callback(true, false);
      let options = {
        feeLimit: parseInt(process.env.FEE_LIMIT)
      };
      tokensToDeposit = tokensToDeposit * 10 ** tokenDecimal;
      tokensToDeposit = MYTronWeb.toBigNumber(tokensToDeposit);
      if (tokenSupportType == 1) return handleWithTRC10();
      handleWithTRC20();
      function handleWithTRC10() {
        options.tokenValue = tokensToDeposit;
        options.tokenId = tokenSupportId;
        deposit();
      }
      function handleWithTRC20() {
        _initTokenContract();
        function _initTokenContract() {
          tronWeb
            .contract()
            .at(tokenSupportAddr)
            .then(_approveTokens)
            .catch(error);
        }
        function _approveTokens(tokenContract) {
          tokenContract
            .approve(collateralContractAddr, tokensToDeposit)
            .send()
            .then(deposit)
            .catch(error);
        } //sellTokensTRC10
      }
      function deposit() {
        self.CONTRACT
          .borrowerDeposit(poolContractAddr, tokensToDeposit)
          .send(options)
          .then(response)
          .catch(error);
      }
      function response() {
        return callback(null, true);
      }  
      function error(e) {
        return callback(e, false);
      }
    },
    borrowerBorrow(poolDetail, amount, callback) {
      let self = this;
      let tokenDecimal     = poolDetail && poolDetail.tokenSupportDecimal ? poolDetail.tokenSupportDecimal : 1; 
      let tokenSupportType = poolDetail && poolDetail.tokenSupportType ? poolDetail.tokenSupportType : -1;
      let poolContractAddr = poolDetail && poolDetail.poolContractAddr ? poolDetail.poolContractAddr : '';
      let tokenSupportAddr = poolDetail && poolDetail.tokenSupportAddr ? poolDetail.tokenSupportAddr : '';
      let collateralContractAddr = poolDetail && poolDetail.collateralContractAddr ? poolDetail.collateralContractAddr : '';
      let tokenSupportId = poolDetail && poolDetail.tokenSupportId ? poolDetail.tokenSupportId : 0; 
      if (tokenSupportType != 1 && tokenSupportType != 2) return callback(true, false);
      let options = {
        feeLimit: parseInt(process.env.FEE_LIMIT)
      };
      amount = MYTronWeb.toSun(amount);
      borrow();
      function borrow() {
        self.CONTRACT
          .borrowerBorrow(poolContractAddr, amount)
          .send(options)
          .then(response)
          .catch(error);
      }
      function response() {
        return callback(null, true);
      }  
      function error(e) {
        return callback(e, false);
      }
    },
    borrowerRepay(poolDetail, amount, callback) {
      let self = this;
      let tokenDecimal     = poolDetail && poolDetail.tokenSupportDecimal ? poolDetail.tokenSupportDecimal : 1; 
      let tokenSupportType = poolDetail && poolDetail.tokenSupportType ? poolDetail.tokenSupportType : -1;
      let poolContractAddr = poolDetail && poolDetail.poolContractAddr ? poolDetail.poolContractAddr : '';
      let tokenSupportAddr = poolDetail && poolDetail.tokenSupportAddr ? poolDetail.tokenSupportAddr : '';
      let collateralContractAddr = poolDetail && poolDetail.collateralContractAddr ? poolDetail.collateralContractAddr : '';
      let tokenSupportId = poolDetail && poolDetail.tokenSupportId ? poolDetail.tokenSupportId : 0; 
      if (tokenSupportType != 1 && tokenSupportType != 2) return callback(true, false);
      amount = MYTronWeb.toSun(amount);
      let options = {
        feeLimit: parseInt(process.env.FEE_LIMIT),
        callValue: amount
      };
      repay();
      function repay() {
        self.CONTRACT
          .borrowerRepay(poolContractAddr)
          .send(options)
          .then(response)
          .catch(error);
      }
      function response() {
        return callback(null, true);
      }  
      function error(e) {
        return callback(e, false);
      }
    },
    borrowerWithdraw(poolDetail) {
      let poolContractAddr = poolDetail && poolDetail.poolContractAddr ? poolDetail.poolContractAddr : '';
      return this.CONTRACT.borrowerWithdraw(poolContractAddr).send({ feeLimit: parseInt(process.env.FEE_LIMIT) });
    }
}



