<script>
import { mapGetters, mapActions } from 'vuex'
import { isMobile } from 'mobile-device-detect';

import MYTronWeb from '../../../js/tron-web.js'

import ConfigCookieKey from '../../../../../config/cookie.key.js'
import ConfigSessionKey from '../../../../../config/session.key.js'

export default {
  data () {
    return {
       language:'en',
    };  
  },
  mounted () {
    this.makeJqueryEvent();  
  },
  methods: {

    /* -----------------------------------------------------------------------------------------
    *  ------------------------------- MAKE JQUERY ---------------------------------------------
    *  -----------------------------------------------------------------------------------------
    */
    makeJqueryEvent() {
      let self = this;
      $(document).ready(function () {
        
        $("body").delegate('#app .content', 'click', function () {
          if ($('.navbar-collapse').hasClass('show') == true) {
            self.hideMobileHeaderMenu();
          }
        });
      });
    },
    /* -----------------------------------------------------------------------------------------
    *  ------------------------------- VERIFY --------------------------------------------------
    *  -----------------------------------------------------------------------------------------
    */
    /* -----------------------------------------------------------------------------------------
    *  ------------------------- MODAL DISPLAY ACTION ------------------------------------------
    *  -----------------------------------------------------------------------------------------
    */
    hideMobileHeaderMenu() {
      if (isMobile == true) {
        $('.icon-mobile-menu').click();
      }
    },
    /* -----------------------------------------------------------------------------------------
    *  ----------------------------------  ACTION ----------------------------------------------
    *  -----------------------------------------------------------------------------------------
    */
    change (to) {
      localStorage['lang'] = to
      this.$i18n.locale = to
      this.language = this.$i18n.locale;
      this.$emit('changeLang', to);
      this.hideMobileHeaderMenu(); 
    },
    /* -----------------------------------------------------------------------------------------
    *  ------------------------------  REDIRECT PAGE -------------------------------------------
    *  -----------------------------------------------------------------------------------------
    */
    redirectToHome() {
      this.$router.push({name:'home'});
    }
  }
}
</script>


