<template>
  <div  style="overflow: hidden;" class="Table-page">         
      <TableView ref='table'/>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
// contract
// components
import TableView from './../../view/Table';


export default {
  name: 'table',
  components: {
   	TableView,
  },
  computed: {
      ...mapGetters([
        
      ])
    },
    created() {    
     	this.initData();
    },
    mounted () {
      
    },
    methods: {
    	...mapActions([
          'initPoolList'
      	]),
      	initData() {
      		this.initPools();
      	},
        initPools() {
          this.initPoolList({ page: 1, limit: 1000 });
        }
    }
}
</script>

<style lang="scss">
</style>


