import MYTronWeb from "./tron-web";
export default {
  CONTRACT_ADDRESS :  process.env.ZTR_GET_BALANCE_CONTRACT_ADDRESS,
  CONTRACT_CALL_DATA : null,
  initContractCallData(tronWebWithZethyrAccount, callback) {
    let self = this;
      tronWebWithZethyrAccount
        .contract()
        .at(self.CONTRACT_ADDRESS)
        .then(response)
        .catch(error);
    function response(tronContract) {
        self.CONTRACT_CALL_DATA = tronContract;
        return callback(true);
    } 
    function error(e) { 
        return callback(false); 
    }
  },
  getBalance(address , callback){
    this.CONTRACT_CALL_DATA
      .getBalance(address)
      .call()
      .then(response)
      .catch(error)
    function response(balance) {
      return callback(null , MYTronWeb.toTrx(parseInt(balance)));
    }
    function error(e) {
      return callback(e, null);
    }
  }
}



