<template>
  <div id="app">
    <div class="header-main">
      <Header v-if="isHomePage() == true"/>
      <HeaderElement v-if="isHomePage() == false" />
    </div>
    <div class="content">
      <router-view ref="app"/>
    </div>
    <div class="footer">
      <Footer/>
    </div>
  </div>
</template>
<script>
/*-----------------Module---------------------------------*/
import { mapGetters, mapActions } from 'vuex'
import { isMobile } from 'mobile-device-detect'
/*-----------------Config---------------------------------*/
/*----------------Layout ---------------------------------*/
import Header  from './components/Layout/Header.vue'
import Footer  from './components/Layout/Footer.vue'
import HeaderElement  from './components/Layout/Element/Header.vue'
import FooterElement  from './components/Layout/Element/Footer.vue'
/*-----------------Tron Web-------------------------------*/
import MYTronWeb from './js/tron-web.js'

import  ZethyrStakeWinContract from './js/zethyr-stake-win.js'
import  ZtrToolContract from './js/zethyr-tool.js'
import  ZethyrLendingContract from './js/zethyr-lending.js'


/*--------------------- Api -------------------------------*/

//
let TronWeb = require('tronweb');
let HttpProvider = TronWeb.providers.HttpProvider;
let fullNode = new HttpProvider(process.env.FUll_NODE);
let solidityNode = new HttpProvider(process.env.SOLIDITY_NODE);
let eventServer = process.env.EVENT_HOST;
let tronWebWithZethyrAccount = MYTronWeb.getTronWeb(TronWeb, process.env.PRIVATE_KEY_ACCOUNT_DEFAULT, fullNode, solidityNode, eventServer);

export default {
  components:{
    Header,
    Footer,
    HeaderElement,
    FooterElement
  },
  data () {
    return {
      isSetupComplated: false
    };
  },
  created() {    
    this.init();
  },
  methods: {
      /* -----------------------------------------------------------------------------------------
      *  ------------------------------- VERIFY PAGE ---------------------------------------------
      *  -----------------------------------------------------------------------------------------
      */
   
      /* -----------------------------------------------------------------------------------------
      *  ------------------------------- INIT CONTRACT -------------------------------------------
      *  -----------------------------------------------------------------------------------------
      */
      init() {
        this.setupContractCallData();
        this.setupContract();
      },
      setupContract() {
        if (typeof(window.tronWeb) === 'undefined') {
          this.reloadSetupContract();
        } else if (tronWeb.fullNode.host != process.env.FUll_NODE && tronWeb.fullNode.host != process.env.FUll_NODE_DEFAULT) {
          this.reloadSetupContract();
        } else if (MYTronWeb.getAccount() == false) {
          this.reloadSetupContract();
        } else if (this.isSetupComplated == false) {
          ZethyrLendingContract.initContract(status => {});
          ZethyrStakeWinContract.initContract(status => {});
          this.isSetupComplated = true;
        }
      },
      reloadSetupContract() {
        let self = this;
        setTimeout(function(){ 
          self.setupContract();
        }, 300);
      },
      setupContractCallData() {
        let self = this;
        Promise
          .all([
            initContractCallData(ZethyrLendingContract),
            initContractCallData(ZtrToolContract),
            initContractCallData(ZethyrStakeWinContract),
          ])
          .then(response)
          .catch(error);
        function response(status) {
          console.log('SUCCESS');
        } 
        function error(e) {
          console.log(e);
        } 
        // internal function
        function initContractCallData(contract) {
          return new Promise((resolve, reject) => {
            contract.initContractCallData(tronWebWithZethyrAccount, is_init => {
              return resolve(is_init);
            });
          });
        }
      },
      /* -----------------------------------------------------------------------------------------
      *  ------------------------------- INIT DATA -----------------------------------------------
      *  -----------------------------------------------------------------------------------------
      */
      isHomePage() {
        let pageName = this.$router.history.current.name;
         if (pageName == 'home') {
            return true;
         }
         return false;
      },
  }
}
</script>

<style lang="scss">
  @import 'assets/css/style.scss';
</style>


